@use '~primitive-ui/src/scss/base/variables' as base;
@use '../variables' as *;

.Navbar {
  &-links {
    display: flex;

    a {
      font-size: 1.75rem;
      font-weight: bold;
      padding: .75rem;

      &.active {
        color: $font-color;
      }
    }
  }
  
  &-search {
    form {
      position: relative;

      svg {
        width: 13px;
        height: 13px;
        position: absolute;
        pointer-events: none;
        top: .5rem;
        left: .5rem;
      }

      input {
        height: 1.875rem;
        padding-left: 1.75rem;
        color: $font-color;
      }
    }
  }

  @media (min-width: #{base.$mobile}) {
    margin-top: 1rem;
    margin-bottom: 1rem;

    &-search {
      align-self: flex-end;
      text-align: right;

      form {
        max-width: 17rem;
        display: inline-block;
      }
    }
  }
}
