@use '~primitive-ui/src/scss/base/variables' as base;
@use '../../variables' as *;

.ListLayout {
  &-empty {
    align-self: center;
    margin: 0 auto;
  }

  &-page {
    display: flex;
    justify-content: space-between;

    &-info span {
      color: $primary-color;
    }
  }

  @media (min-width: #{base.$mobile}) {
    &-list > div {
      padding-left: 0;
      margin-bottom: 1rem;
    }
  }
}
