@use '~primitive-ui/src/scss/base/variables' as base;
@use '../../variables' as *;

$item-padding: 1.75rem 1.5rem;

.ItemExcerpt {
  position: relative;
  height: 26.5rem;
  border-radius: 6px;
  box-shadow: 20.128px 13.071px 9px rgba(8,27,43,0.27);
  overflow: hidden;

  svg {
    stroke: $svg-color;
    fill: none;
  }

  &-related, &-last, &-title, &-info, &-buttons {
    position: absolute;
    z-index: 1;
  }

  &-related, &-last, &-title, &-info, &-buttons {
    pointer-events: none;
  }

  &-related, &-last {
    top: nth($item-padding, 1);
  }

  &-related, &-title, &-info {
    left: nth($item-padding, 2);
  }

  &-last, &-title, &-buttons {
    right: nth($item-padding, 2);
  }

  &-info, &-buttons {
    bottom: nth($item-padding, 1);
  }

  &-related {
    border-radius: 10px;
    border: 1px solid white;
    font-size: 0.625rem;
    padding: .15rem .8rem;
  }

  &-last {
    border-radius: 2px;
    background-color: #931636;
    padding: .3rem .8rem;
    text-transform: uppercase;
    font-size: .5rem;
    font-weight: bold;
  }

  &-title {
    bottom: 5rem;

    h3 {
      margin: .5rem 0;
    }

    p {
      text-overflow: ellipsis;
      overflow: hidden;
      height: 1.2em;
      white-space: nowrap;
      color: rgba($font-color, .7);
    }
  }

  &-info  {
    display: flex;

    > div {
      text-align: center;
      white-space: nowrap;
      padding: 0 .6rem;

      &:first-child {
        padding-left: 0;
      }

      &:not(:last-child) {
        border-right: 1px solid rgba($font-color, .08);
      }
    }
  }

  &-read-news, &-min-read {
    font-size: .75rem;
    font-weight: bold;
  }

  &-read-news svg {
    vertical-align: bottom;
    margin-right: .6rem;
    stroke: rgba($primary-color, .6);
  }

  &-datetime {
    font-size: .8rem;
    color: rgba($font-color, .7);
  }

  &-min-read {
    color: rgba($font-color, .35);
  }

  &-buttons {
    margin-bottom: -.6rem;
  }

  &-link, .BookmarkButton svg {
    stroke-width: 2;
    margin: .4rem;
  }

  &-link {
    stroke-linecap: round;
  }

  a {
    display: block;
    height: 100%;

    > img {
      height: 100%;
      width: 100%;
      object-fit: cover;
    }

    &::after {
      content: linear-gradient(180deg, rgba(28,58,82,0) 0%, #05141b 100%);
      position: absolute;
      display: block;
      top: 0;
      width: 100%;
      height: 100%;
    }
  }
}

@media (min-width: #{base.$mobile}) {
  .NewsList-last .ItemExcerpt {
    height: 39rem;

    h3 {
      font-size: 1.5rem;
    }
  }
}
